.profile__name {
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  margin: 0 16px 0 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  max-width: 400px;
}

@media screen and (max-width: 767px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    margin: 0 10px 0 0;
    max-width: 254px;
  }
}